import Layout from '../component/layout'
import useTags from 'hooks/useTags'
import styled from 'styled-components'
import Icon from '../component/icon'
import { Link } from 'react-router-dom'
import Button from 'component/Button'

import Top from '../component/Header'
const TagList = styled.ol`
font-size: 16px;
background: white;
> li {
  margin-left: 16px;
  line-height: 20px;
  border-bottom: 2px solid #d5d5d9;
  align-items: center;
  position: relative;
  > a {
    display: flex;
    justify-content: space-between; 
    align-items: center;
    padding: 11px 16px 12px 0;
  }
}

`

const Center = styled.div`
display: flex;
align-items: center;
justify-content: center;
flex-direction: column;

`
const Space = styled.div`
height: 16px;

`
const Icons = styled(Icon)`
position: absolute;
margin-left: 60px;

`
const Tags = () => {
  const { tags, setTags, onAddTag } = useTags()
  return (
    <Layout>
      <Top />

      <TagList>
        {
          tags.map(tag =>
            <li key={tag.id}>


              <Link to={'/tags/' + tag.id}>
                {tag.name}
                <Icons name={tag.name} />
                <Icon name="right" />
              </Link>
            </li>)

        }
      </TagList>
      <Center>
        <Space />
        <Space />
        < Button onClick={onAddTag}>新增标签</ Button>
        <Space />
      </Center>
    </Layout>
  )
}
export default Tags