import styled from "styled-components";
import React, { useState } from "react";
import { types } from "@babel/core";


const Wrapper = styled.section`
 > ul {
  display: flex;
  background: #ffda47;
 
  > li {
width: 50%;
text-align: center;
font-size: 24px;
padding: 16px 0;
position: relative;
&.selected::after {
content: '';
display: block;
height: 3px;
background: black;
position:absolute;
bottom: 0; 
width: 100%;
left: 0;

}
  }
 }
`
type Props = {
  value: '-' | '+',
  onChange: (type: '+' | '-') => void
}
const TypeSelection: React.FC<Props> = (props) => {
  const type = props.value
  return (
    <Wrapper>
      <ul>
        <li className={type == '-' ? 'selected' : ''}
          onClick={() => { props.onChange('-') }}
        >支出</li>
        <li className={type == '+' ? 'selected' : ''}
          onClick={() => { props.onChange('+') }}
        >收入</li>
      </ul>
    </Wrapper >
  )
}
export default TypeSelection