import Layout from '../component/layout'
import styled from 'styled-components'
import React, { useState } from 'react'
import { TagsSelection } from './Money/TagSelection'
import NotesSelection from './Money/NotesSection'
import TypeSelection from './Money/TypeSelection'
import NumberPadSelection from './Money/NumberPadSelection'
import useRecords from 'hooks/useRecords'
import Header from '../component/Header'


const TypeWrapper = styled.div`

background:#c4c4c4;
`
const MyLayout = styled(Layout)`
display: flex;
flex-direction: column;
`

type Types = '-' | '+'
const defaultFormDate = {
  tagIds: [] as number[],
  note: '',
  Type: '-' as Types,
  amount: 0
}
const Money = () => {
  const [value, setSelectedTags] = useState({
    tagIds: [] as number[],
    note: '',
    Type: '-' as Types,
    amount: 0
  })
  const { records, addRecord } = useRecords()

  type Selected = typeof value
  const onChange = (obj: Partial<Selected>) => {
    setSelectedTags({
      ...value,
      ...obj
    })

  }
  const submit = () => {
    if (addRecord(value)) {
      alert('保存成功')
      setSelectedTags(defaultFormDate)

    } else {
      alert('保存失败')
    }
  }
  return (
    <MyLayout scrollTop={9999}>
      <Header />
      <TagsSelection value={value.tagIds}
        onChange={(tagIds) => onChange({ tagIds })}
      />

      <NotesSelection value={value.note}
        onChange={(note) => onChange({ note })}
      />
      <TypeWrapper>
        <TypeSelection value={value.Type}
          onChange={(type) => {
            setSelectedTags({
              ...value,
              Type: type
            })
          }}
        />
      </TypeWrapper>

      <NumberPadSelection value={value.amount}
        onChange={(amount) => {
          onChange({ amount })
        }}
        onOK={() => { submit() }}
      />
    </MyLayout>
  )
}
export default Money