import styled from "styled-components";
import React, { useState } from "react";
import Wrapper from "./NumberPadSelection/Wrapper";
import computeOutput from "./NumberPadSelection/computeOutput";
import Icon from '../../component/icon'

type Props = {
  value: number,
  onChange: (value: number) => void
  onOK?: () => void
}
const Number = styled(Icon)`
position: absolute;
display: flex;
align-items: center;
margin-top: 15px;

`
const NumberPadSelection: React.FC<Props> = (props) => {
  // const output = props.value.toString()

  const [output, _setOutput] = useState(props.value.toString())
  const setOutput = (output: string) => {
    let newOutput: string
    if (output.length > 16) {
      newOutput = output.slice(0, 16)
    } else if (output.length === 0) {
      newOutput = '0'
    } else {
      newOutput = output
    }
    _setOutput(newOutput)
    props.onChange(parseFloat(newOutput))
  }
  const onClickButtonWrapper = (e: React.MouseEvent) => {
    const text = (e.target as HTMLButtonElement).textContent
    if (text === null) { return }
    if (text === 'ok') {
      if (props.onOK) { props.onOK() }
    }
    setOutput(computeOutput(text, output))
  }
  return (
    <Wrapper>
      <div className='output'>
        <Number name="number" />
        {output}
      </div>
      <div className='pad' onClick={onClickButtonWrapper}>
        <button>1</button>
        <button>2</button>
        <button>3</button>
        <button>删除</button>
        <button>4</button>
        <button>5</button>
        <button>6</button>
        <button>清空</button>
        <button>7</button>
        <button>8</button>
        <button>9</button>
        <button className='ok'>ok</button>
        <button className='zero'>0</button>
        <button className='dot'>.</button>
      </div>
    </Wrapper>
  )
}
export default NumberPadSelection