import styled from 'styled-components'
import {
  Link,
  NavLink
} from 'react-router-dom'

import Icon from './icon'


const NavWrapper = styled.nav`

line-height: 24px;
box-shadow: 0 0 3px rgba(0, 0, 0, 0.25);
> ul {
  display: flex;
> li {
  
  width: 33.33333%;

  padding: 5px;
 
  > a{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    padding: 4px 0;
    &.selected {
      color: #b62b00;
      .icon {
        fill: #b62b00;
      }
    }
    .icon {
      width: 24px;
      height: 24px;
        }
        
  }
  
}
display: flex;
`
const Nav = () => {
  return (
    <NavWrapper>
      <ul>
        <li>
          <NavLink to="/tags" exact activeClassName='selected'>
            <Icon name="tag" />
            标签页</NavLink>
        </li>
        <li>

          <NavLink to="/money" activeClassName='selected'>
            <Icon name="money" />
            记账页</NavLink>
        </li>
        <li>


          <NavLink to="/statistics" activeClassName='selected'>
            <Icon name="chart" />
            统计页</NavLink>
        </li>
      </ul>
    </NavWrapper>
  )
}
export default Nav;