import { useState } from 'react'
import Layout from '../component/layout'
import TypeSelection from './Money/TypeSelection'
import styled from 'styled-components'
import useRecords from 'hooks/useRecords'
import useTags from 'hooks/useTags'
import day from 'dayjs'
import { RecordItem } from 'hooks/useRecords'

// import day from 'dayjs'
const TypeWrapper = styled.div`
background: white;
`
const Item = styled.div`
background: white;
font-size: 18px;
line-height: 20px;
padding: 10px 16px;
display: flex;
justify-content: space-between;
> .note {
  margin-right: auto;
  margin-left : 16px;
  color: #999; 
}

`
const Header = styled.h3`
font-size: 18px;
line-height: 20px;
padding: 10px 16px;


`
const Chart = () => {
  const [Type, setType] = useState<'-' | '+'>('-')
  const { records } = useRecords()
  const { getName } = useTags()
  const SelectType = records.filter(r => r.Type === Type)
  const hash: { [k: string]: RecordItem[] } = {}
  SelectType.map(r => {
    const key = day(r.createAt).format('YYYY年-MM月-DD日')

    if (!(key in hash)) {
      hash[key] = []
    }
    hash[key].push(r)
  })
  const array = Object.entries(hash).sort((a, b) => {
    if (a[0] === b[0]) return 0
    if (a[0] > b[0]) return -1
    if (a[0] < b[0]) return 1
    return 0
  })
  return (
    <Layout>

      <TypeWrapper>
        <TypeSelection value={Type}
          onChange={(value) => setType(value)}
        />
      </TypeWrapper>
      {array.map(([date, records]) => <div>

        {date}

        <div>
          {records.map(r => {
            return <Item key={r.createAt}>
              <div className="tags">{r.tagIds.map(tagId => <span key={tagId}>{getName(tagId)}</span>)

              }</div><hr />
              <div className="note">
                {r.note ? r.note : ''}
              </div>
              <div className="amount">
                ￥{r.amount}

              </div>

            </Item>
          })}
        </div>
      </div>)}
      <div>

      </div>
    </Layout>
  )
}
export default Chart