type Props = {
  name?: string
} & React.SVGAttributes<SVGElement>

const Icon = (props: Props) => {
  const { name, children, className, ...rest } = props
  return (
    <svg className={'icon ' + className || ''} {...rest}>
      <use xlinkHref={'#' + props.name}>

      </use>
    </svg>
  )
}
export default Icon