import styled from "styled-components"
import React, { useState } from 'react'
import useTags from "hooks/useTags"
import CreateId from "lib/createId"
import Icon from '../../component/icon'
const Wrapper = styled.section`
background: #ffffff;
padding: 12px 16px;
flex-grow: 1;
display: flex;
flex-direction: column;
justify-content: flex-end;
align-items: flex-start;
> ol {
 margin: 0 -12px;
  > li {
    display: inline-block;
    background: #f0f4f7;
    border-radius: 18px;
    padding: 3px 18px;
    font-size: 14px;
    margin: 8px 12px;
   
    &.selected {
      background: #ffda47; 
    }
  
    
  }

}
> button {
  background: none;
  border: none;
  border-bottom: 1px solid #333;
  padding: 2px 4px;
  color: #666;
  margin-top: 8px;
  }
`
type Props = {
  value: number[]
  onChange: (selected: number[]) => void
}
const TagsSelection: React.FC<Props> = (props) => {

  const { tags, setTags, onAddTag } = useTags()
  const selectedTagIds = props.value

  const onToggleTag = (tagId: number) => {
    const index = selectedTagIds.indexOf(tagId)
    console.log('11111')
    if (index >= 0) {
      props.onChange(selectedTagIds.filter(t => t !== tagId))
    } else {
      props.onChange([...selectedTagIds, tagId])
    }
  }
  return (
    <Wrapper>

      <ol>
        {tags.map(tags =>
          <li key={tags.id} onClick={() => onToggleTag(tags.id)}
            className={selectedTagIds.indexOf(tags.id) >= 0 ? 'selected' : ''}
          ><Icon name={tags.name} />{tags.name}</li>
        )}


      </ol>
      <button onClick={onAddTag}>新增标签</button>

    </Wrapper>
  )
}
export { TagsSelection }