import React from "react";
import styled from 'styled-components'
import Icon from '../component/icon'


const TopBar = styled.header`
display: flex;
justify-content: center;
line-height: 20px;
padding: 14px;
text-align: center;
background: #ffda47;
height: 50px;
align-items: center;
position: relative;
`
const CustomIcon = styled(Icon)`
position: absolute;
margin-top: 10px;
height: 150px;
width: 150px;
`;
const Top = () => {
  return (
    <TopBar>
      <CustomIcon name="logo" />
    </TopBar>
  )
}
export default Top