import Layout from "component/layout";
import React from "react";
import { useParams, useHistory } from "react-router-dom";
import useTags from "hooks/useTags";
import Icon from '../component/icon'
import Button from '../component/Button'
import styled from 'styled-components'

const TopBar = styled.header`
display: flex;
justify-content: space-between;
line-height: 20px;
padding: 14px;
background: #ffda47;
align-items: center;
`
const Label = styled.label`
label {
  display: flex;
  align-items: center;
  > span {
margin-right: 16px;
white-space: nowrap;
  }
  > input {
    display: block;
    width: 100%;
    height: 60px;
    background: none;
    border: none;
}
`
const InputWrapper = styled.div`

background: white;
padding: 0 16px;
margin-top: 8px;

`
const Center = styled.div`
display: flex;
justify-content: center;
align-items: center;
flex-direction : column;
bottom: 0;
`
const Space = styled.div`
  height: 16px;
`


type Params = {
  id: string
}
type Props = {

}


const TagEdit: React.FC<Props> = (props) => {
  const { tags, findTag, updateTag, deleteTag } = useTags()
  const { id } = useParams<Params>()
  const history = useHistory()
  const tag = findTag(parseInt(id))
  const onclickBack = () => {
    history.goBack()
  }

  return (
    <Layout>
      <TopBar>
        <Icon name="left" onClick={onclickBack}></Icon>
        <span>编辑标签</span>
        <Icon />
      </TopBar>
      {tag ? <div>
        <Label>
          <InputWrapper>
            <label>
              <span>标签名</span>
              <input type="text" placeholder={tag.name} onChange={(e) => {
                updateTag(tag.id, { name: e.target.value })
              }} />
            </label>
          </InputWrapper>
        </Label>
      </div> : <Center><Space /><Space /><Space />tag 不存在</Center>}
      <div>
        <Center>
          < Space />
          < Space />
          < Space />
          <Button onClick={(id) => { deleteTag(tag.id) }}>删除标签</Button>
        </Center>
      </div>
    </Layout>
  )
}

export default TagEdit